import {create} from 'zustand';
import {isEmpty} from 'lodash';
interface MenuItem {
  menuId: number;
  menuParentId: number;
  title: string;
  serviceId: number;
  serviceName: string;
  activated: number;
  nameId: string;
  type: 'button' | 'menu' | 'path';
  description: string;
  view: string;
  icon: string;
}

interface ZutandStore {
  listMenuView: MenuItem[];
  setListMenuView: (listMenuView: MenuItem[]) => void;
}

const initialSettingState = {
  listMenuView: [],
};

export const useZutandStore = create<ZutandStore>((set, get) => ({
  ...initialSettingState,
  setListMenuView: (listMenuView: MenuItem[]) => {
    set({listMenuView: listMenuView});
  },
}));
